  // AUto Genrat


  labourRates() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listPBLaborrate/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var labourRates = data.json();
        if (labourRates.status == 200) {
          this.labourList = labourRates.response;
          for (var i = 0; i < this.labourList.length; i++) {
            this.labourList[i].subTotal = this.labourList[i].listPrice * this.labourList[i].unitCost
          }
          this.labourStatus = true
        } else {
          this.labourStatus = false
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
