  /* /Add Inventory - Popup */
  editData(x) {
    this.laborHour = x.time
    this.qty = x.time
    this.finalMemberValue = x.member
    this.listPriceInventory = x.materialCost
    this.editPriceBook1 = this.formBuilder.group({
      proId: [x.proId, Validators.required],
      serviceId: [x.serviceId, Validators.required],
      category: [x.category, Validators.required],
      categoryCode: [x.categoryCode, Validators.required],
      subcatogryCode: [x.subcatogryCode, Validators.required],
      subCategory: [x.subCategory, Validators.required],
      description: [x.description, Validators.required],
      materialCost: [x.materialCost, Validators.required],
      truckOperatingCost: [x.truckOperatingCost, Validators.required],
      leadCost: [x.leadCost, Validators.required],
      totalCost: [x.totalCost, Validators.required],
      taxPercentage: [x.taxPercentage, Validators.required],
      taxValue: [x.taxValue, Validators.required],
      totalmaterialPrice: [x.totalmaterialPrice, Validators.required],
      time: [x.time, Validators.required],
      laborRate: [x.laborRate, Validators.required],
      laborCost: [x.laborCost, Validators.required],
      standard: [x.standard, Validators.required],
      value: [x.value, Validators.required],
      member: [x.member, Validators.required],
      instructions: [x.instructions, Validators.required],
      serviceDescription: [x.serviceDescription, Validators.required],
      listPrice: [x.listPrice, Validators.required],
      document: [x.document, Validators.required],
      // document: Global.imagePath,
    })
    this.nofileData = true
    this.documentLink = x.document
  }
  viewPriceBookService() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewPriceBook' + '/' + this.serviceIdData, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.priceBookListDetails = response.response[0];
          this.priceBookServiceResponse = true;
          this.editData(this.priceBookListDetails)
          this.changeCategory(response.response[0].category)
          this.priceBookCategory = response.response[0].category
          this.priceBookTitle = response.response[0].title
          this.totalCostNew = response.response[0].totalCost
          this.subTotalLR = response.response[0].laborCost
          this.urls = this.priceBookListDetails.picture.split(',')
          if (this.priceBookListDetails.picture == "" || this.priceBookListDetails.picture == null || this.priceBookListDetails.picture == undefined || this.priceBookListDetails.picture == "undefined") {
            this.imagePreview = '../../assets/img/no-image.png'
            var index = this.imagePreview.indexOf(this.imagePreview)
            this.removeSelectedFile(index)
          }
          else {
            for (var i = 0; i < this.urls.length; i++) {
              this.chosseTypecount = this.urls.length
              this.imageArray.push(this.urls[i])
              this.imageNames.push({ image: this.urls[i] });
              this.arrayValues.push(this.urls[i])
              this.imagePreview = Global.url + "/" + this.urls[i]
              this.arr.push({ name: this.imagePreview });
              this.newArray = this.arrayValues
              this.uniqueArray = this.arrayValues
            }
            this.fileName = this.imageArray.length
          }
        }
        else {
          this.priceBookServiceResponse = false;
        }
        error => {
        }
      })
  }


  changeCategory(x) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/PBSubcategoryData/' + this.loginData.loginCode + "/" + x, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.subCategoryServices = response.response
          // this.getCategoryName(x)
          return false;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }



