import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
// import { ImageUpload } from '../../../../utilities/imageupload'
import { Global } from '../../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
import { NgxGalleryOptions, NgxGalleryImage, NgxGalleryAnimation } from 'ngx-gallery';
declare var $: any;
@Component({
  providers: [Beyond],
  selector: 'app-view-item',
  templateUrl: './view-item.component.html',
  styleUrls: ['./view-item.component.scss']
})
export class ViewItemComponent implements OnInit {
  galleryOptions: NgxGalleryOptions[];
  dataGallery: boolean = false;
  galleryImages = [];
  bellerImage1: string = "assets/img/toi.jpg";
  bellerImage2: string = "assets/img/slideimg1.png";
  bellerImage3: string = "assets/img/plumbing/pipe1.png";
  bellerImage4: string = "assets/img/plumbing/plumbing1.png";
  serviceIdData: any;
  priceBookListDetails: any;
  priceBookServiceResponse: boolean;
  urls: any = []
  // imagePreview: any;
  imagePreview: string = "assets/img/no-image.png"
  displayFileModal = 'none';
  addToEstimate: boolean;
  arr: any = [];
  imageName: any = [];
  showModal: boolean;
  Image: any=[];
  index: any;
  myThumbnail = "https://wittlock.github.io/ngx-image-zoom/assets/thumb.jpg";
  myFullresImage = "https://wittlock.github.io/ngx-image-zoom/assets/fullres.jpg";
  imageData: any=[];
  withOutImg: boolean;
  withImg: boolean;
  constructor(public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.serviceIdData = this.route.snapshot.params['serviceId']
    //console.log("serviceIdData"+this.serviceIdData)
    this.getImagesGallery()
    this.viewPriceBookService()
    this.AddtoEstimate()
    this.galleryOptions = [
      {
        width: '400px',
        height: '400px',
        thumbnailsColumns: 4,
        imageAnimation: NgxGalleryAnimation.Slide,
        previewZoom: true,
        previewRotate: true
      },
      {
        breakpoint: 800,
        width: '70%',
        height: '600px',
        imagePercent: 80,
        thumbnailsPercent: 20,
        thumbnailsMargin: 20,
        thumbnailMargin: 20
      },
      {
        breakpoint: 400,
        preview: false
      }
    ];
  }
  getImagesGallery() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfProzDetails', options)
      .subscribe((dataPDF) => {
        var galleryData = dataPDF.json();
        if (galleryData.status == 200) {
          //console.log(galleryData)
          this.dataGallery = true;
          for (var i = 0; i < galleryData.response.length; i++) {
            this.galleryImages.push({
              small: galleryData.response[i].image,
              medium: galleryData.response[i].image,
              big: galleryData.response[i].image,
              //description: this.dateConversion(galleryData.response[i].newDate())
            })
          }
        }
      })
  }

  dateConversion(todayTime) {
    var date = new Date(todayTime)
    var month = date.getMonth() + 1;
    var day = date.getDate();
    var year = date.getFullYear();
    return day + "-" + month + "-" + year;
  }



  viewPriceBookService() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewPriceBook' + '/' + this.serviceIdData, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.priceBookListDetails = response.response[0];
          console.log(" view priceBookListDetails" + JSON.stringify(this.priceBookListDetails))
          this.priceBookServiceResponse = true;
           this.urls = this.priceBookListDetails.picture.split(',')
          for (var i = 0; i < this.urls.length; i++) {
            this.galleryImages.push({
              small: this.urls[i],
              medium: this.urls[i],
              big: this.urls[i],
              //description: this.dateConversion(galleryData.response[i].newDate())

            })
            console.log("document1" + JSON.stringify(this.galleryImages))
          }
          console.log("Categoyr" + JSON.stringify(this.urls))

          if (this.priceBookListDetails.picture == "" || this.priceBookListDetails.picture == null || this.priceBookListDetails.picture == undefined || this.priceBookListDetails.picture == "undefined") {
            this.imagePreview = '../../assets/img/no-image.png'
            var index = this.imagePreview.indexOf(this.imagePreview)
            console.log("TTTTTT" + index)
            this.withImg =  false;
            this.withOutImg = true;
          }
          else {
            this.withImg=true;
            this.withOutImg=false;
            for (var i = 0; i < this.urls.length; i++) {
              this.imageName.push({ image: this.urls[i] });
              console.log("imageNameimageName" + JSON.stringify(this.imageName))
              this.imagePreview = Global.url + "/" + this.urls[i]
              this.arr.push({ name: this.imagePreview });
            }
          }
        }
        else {
          this.priceBookServiceResponse = false;
        }
        error => {
        }
      })
  }

  backToPriceBook() {
    if (this.imageData = this.Image ){
      $("#myModal0").modal("hide"); 
    }
  this.router.navigate(['/pricebook/listPriceBook'])
  }
  viewDocument() {
    this.displayFileModal = 'block'
  }
  onCloseHandled() {
    this.displayFileModal = 'none';
  }
  onFileSelected() {
    let $img1: any = document.querySelector('#file');

    if (typeof (FileReader) !== 'undefined') {
      let reader = new FileReader();

      reader.onload = (e: any) => {
        this.imagePreview = e.target.result;
      };

      reader.readAsArrayBuffer($img1.files[0]);
    }
  }

  addToEstimateButton() {
    this.router.navigate(['/estimate/addEstimate'])
  }
  AddtoEstimate() {
    //console.log("this ismain category checking" + JSON.stringify(this.serviceIdData))
    if (this.serviceIdData == false) {
      this.addToEstimate = true
      //console.log("this......true"+this.addToEstimate)
    }
    else {
      this.addToEstimate = false
      //console.log("this...... false"+this.addToEstimate)
    }
  }

  /* Image showing in pop up */
  show(index) {
       console.log("index::::"+JSON.stringify(index.name))
    this.Image=index.name.substring(22, 50)
    console.log("Image::::" + JSON.stringify(this.Image))
    for(var i =0 ;i<this.arr.length ;i++){
      this.imagePreview[i]
      console.log("this.arr[i].name:::" + this.arr[i].name)
    
    if (index.name == this.arr[i].name){
      this.imageData=index.name 
     // this.Image//=index.substring(22, 50)
      console.log("imagePreview" + this.Image)
    this.showModal = true;
       $("#myModal0").modal("show"); 
    }
  
    else{
       // $("#myModal0").modal("hide"); 
    }
  }
  }
  /* Image showing in pop up */
  //Bootstrap Modal Close event
  hide() {
    $("#myModal0").modal("hide"); 
  }
}
