import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';
import { Global } from '../../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';


@Component({
  providers: [Beyond],
  selector: 'app-view-pricebook-services',
  templateUrl: './view-pricebook-services.component.html',
  styleUrls: ['./view-pricebook-services.component.scss']
})
export class ViewPricebookServicesComponent implements OnInit {
  bellerImage1: string = "assets/img/plumbing/House1.png";
  bellerImage2: string = "assets/img/plumbing/House3.png";
  bellerImage3: string = "assets/img/plumbing/pipe1.png";
  bellerImage4: string = "assets/img/plumbing/plumbing1.png";
  loginData: any;

  constructor(public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService) { }

  ngOnInit() {
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.beyond.beyondAdmin()

  }

  okPriceBook() {
    this.router.navigate(['/pricebook/listPriceBook'])
  }
  editPriceBook() {
    this.router.navigate(['/pricebook/editPriceBook'])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list(){
    this.router.navigate(['/pricebook/listPriceBook'])
  }
}
